import React from "react";
import styles from "./styles/App.module.css";

function App() {
  return (
    <div className={styles.app}>
      {/* Navbar */}
      <nav className={styles.navbar}>
        <div className={styles.navLeft}>
          <img src="/my-app/src/images/logo.png" alt="CONNECT-ED" className={styles.logo} />
          <span className={styles.brandName}></span>
        </div>
        
        <div className={styles.navCenter}>
          <a href="/" className={styles.active}><i className="home-icon"></i> Home</a>
          <a href="/meetups"><i className="meetup-icon"></i> Meetups</a>
          <a href="/communities"><i className="community-icon"></i> Communities</a>
          <a href="/network"><i className="network-icon"></i> Network</a>
        </div>

        <div className={styles.navRight}>
          <div className={styles.searchContainer}>
            <input type="search" placeholder="Search" className={styles.searchInput} />
          </div>
          <button className={styles.signInBtn}>Sign In</button>
        </div>
      </nav>

      {/* Main Content */}
      <div className={styles.mainContainer}>
        {/* Left Column */}
        <div className={styles.leftColumn}>
          <div className={styles.quickStats}>
            <h2>Quick Stats</h2>
            <div className={styles.statsGrid}>
              <div className={styles.statItem}>
                <h3>1.2K</h3>
                <p>Post Views</p>
              </div>
              <div className={styles.statItem}>
                <h3>85%</h3>
                <p>Profile Growth</p>
              </div>
              <div className={styles.statItem}>
                <h3>45</h3>
                <p>New Connections</p>
              </div>
              <div className={styles.statItem}>
                <h3>12</h3>
                <p>Pending Invites</p>
              </div>
            </div>

            <div className={styles.peopleSection}>
              <h2>People You May Know</h2>
              <div className={styles.peopleList}>
                {[
                  { name: 'Srishti Kapoor', period: '2022-Present' },
                  { name: 'Akhil Sharma', period: '2013-2017', title: 'Senior Developer at Tech Mahindra' },
                  { name: 'Shruti Arora', period: '2018-2022', title: 'Senior Consultant at EY' },
                  // ... more people
                ].map((person, index) => (
                  <div key={index} className={styles.personCard}>
                    <img src={`/avatars/${index}.jpg`} alt={person.name} className={styles.avatar} />
                    <div className={styles.personInfo}>
                      <h4>{person.name}</h4>
                      <p>{person.period}</p>
                      {person.title && <p>{person.title}</p>}
                    </div>
                    <button className={styles.connectBtn}>+</button>
                  </div>
                ))}
              </div>
              <button className={styles.seeMoreBtn}>See more connections</button>
            </div>
          </div>
        </div>

        {/* Center Column */}
        <div className={styles.centerColumn}>
          <div className={styles.createPost}>
            <div className={styles.postInput}>
              <img src="/avatar.jpg" alt="User" className={styles.userAvatar} />
              <input type="text" placeholder="What's on your mind?" />
            </div>
            <div className={styles.postActions}>
              <button><i className="media-icon"></i> Media</button>
              <button><i className="live-icon"></i> Live</button>
              <button><i className="poll-icon"></i> Poll</button>
              <button><i className="story-icon"></i> Story</button>
              <button><i className="golive-icon"></i> Go Live</button>
            </div>
          </div>

          {/* Posts Feed */}
          <div className={styles.feed}>
            <div className={styles.post}>
              <div className={styles.postHeader}>
                <img src="/michael.jpg" alt="Michael Chen" />
                <div>
                  <h3>Michael Chen</h3>
                  <p>Product Manager at Innovation Labs</p>
                  <span>2h ago</span>
                </div>
              </div>
              <p className={styles.postContent}>
                Excited to announce our latest product feature release! After months of hard work,
                we've launched a game-changing update that will revolutionize how our users interact
                with the platform. #ProductDevelopment #Innovation
              </p>
              <img src="/product-image.jpg" alt="Product" className={styles.postImage} />
              <div className={styles.postActions}>
                <button>Like</button>
                <button>Comment</button>
                <button>Share</button>
              </div>
            </div>
          </div>
        </div>

        {/* Right Column */}
        <div className={styles.rightColumn}>
          <div className={styles.upcomingMeetups}>
            <h2>Upcoming Meetups</h2>
            {[
              {
                title: 'Tech Innovation Summit 2024',
                date: 'DEC 15',
                time: '10:00 AM PST',
                type: 'Virtual',
                tag: 'Technology'
              },
              // ... more meetups
            ].map((meetup, index) => (
              <div key={index} className={styles.meetupCard}>
                <div className={styles.meetupDate}>
                  <span>{meetup.date}</span>
                </div>
                <div className={styles.meetupInfo}>
                  <h4>{meetup.title}</h4>
                  <p>{meetup.type} Â· {meetup.time}</p>
                  <span className={styles.tag}>{meetup.tag}</span>
                </div>
              </div>
            ))}
            <button className={styles.seeMoreBtn}>See more events</button>
          </div>

          <div className={styles.communities}>
            <h2>Communities</h2>
            {/* Communities list */}
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;